<template>
    <div class="settings-container account-delete-confirmation">
        <transition name="fade">
            <div v-show="dataSaving" class="loading-overlay">
                <loader></loader>
            </div>
        </transition>
        <div class="wtw-container center">
            <div class="step-container">
                <div
                    class="back-to-main-menu"
                    @click.click="goToAccountSettings"
                >
                    <span class="stay-black">Back</span>
                </div>
                <div class="step-title-container">
                    <div class="step-title">
                        Are you sure you want to delete your account?
                    </div>
                    <div>
                        If you do, your personalized outfits, lists, data, and
                        more will be deleted permanently.
                    </div>
                </div>
            </div>
            <div class="step-actions">
                <button
                    class="button pink small-font"
                    @click.click="goToAccountSettings"
                >
                    Cancel
                </button>

                <button class="delete-account-btn" @click.click="deleteAccount">
                    Delete account
                </button>
            </div>
        </div>
        <AccountDeletedModal v-if="showModal" />
    </div>
</template>

<script>
import { mapGetters } from "vuex";
import AccountDeletedModal from "./account-deleted-modal.vue";
import Loader from "../../common/loader.vue";

import $ from "webpack-zepto";

export default {
    components: { AccountDeletedModal, Loader },
    data() {
        return { dataSaving: false, showModal: false };
    },
    computed: {
        ...mapGetters({
            user: "auth/getUser",
        }),
    },
    created() {
        $(window).scrollTop(0);
    },
    methods: {
        goToAccountSettings() {
            this.$store.commit({
                type: "menu/setCurrentMenuItem",
                item: {
                    menuContent: "account-settings",
                    component: "account-settings/account-settings.vue",
                    isMenuExpanded: true,
                },
            });
        },
        deleteAccount() {
            this.dataSaving = true;
            this.$store
                .dispatch("account/deleteAccount", this.user)
                .then(() => {
                    this.$store.commit("cart/resetState");
                    this.$store.commit("filters/resetState");
                    this.$store.commit("generatedOutfits/resetState");
                    this.$store.commit("onboarding/resetState");
                    this.$store.commit("products/resetState");
                    this.$store.commit("savedOutfits/resetState");
                    this.$store.commit("savedProducts/resetState");
                    this.$store.commit("outfits/resetState");
                    this.dataSaving = false;
                    this.showModal = true;
                })
                .catch(() => {
                    this.dataSaving = false;
                });
        },
    },
};
</script>
