.back-to-main-menu {
    margin-bottom: 30px;
}
.menu-container {
    .step-title-container {
        .step-title {
            font-size: 20px;
            font-weight: 600;
        }
    }
}
.step-actions {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin-top: 30px;
    margin-bottom: 20px;
}
.menu-container {
    .settings-container {
        .button.pink {
            margin-bottom: 0;
        }
    }
}
.delete-account-btn {
    color: #ff0000;
    font-size: 12px;
}
</style>
